/* eslint-disable comma-dangle */
<template>
  <div class="wrapper">
    <div class="deck">
      <SmallCard v-for="value in decks.get(selectedDeck)" :key="value" :cardValue="value"/>
    </div>
  </div>
</template>

<script>
import SmallCard from './SmallCard.vue';
import { DECK_MAP, DECK_STANDARD } from '../data/deck';

export default {
  name: 'Deck',
  components: {
    SmallCard,
  },
  props: { deck: { default: 'standard' } },
  data() {
    return {
      decks: DECK_MAP,
    };
  },
  computed: {
    selectedDeck() {
      return this.deck || DECK_STANDARD;
    },
  },
};
</script>

