<template>
<div @click="reveal">
    <div class="card green" v-if="cardValue === 'Green'">
    </div>

    <div class="card yellow" v-else-if="cardValue === 'Yellow'">
    </div>

    <div class="card amber" v-else-if="cardValue === 'Amber'">
    </div>

    <div class="card purple" v-else-if="cardValue === 'Purple'">
    </div>

    <div class="card red" v-else-if="cardValue === 'Red'">
    </div>

    <div class="card" v-else-if="cardValue === 'coffee'">
      <span class="card-image" >
        <img  src="../assets/coffee.png" >
      </span>
    </div>

    <div class="card" v-else>
      <span class="card-text">{{ cardValue }}</span>
    </div>

  </div>
</template>

<script>

export default {
  name: 'Card',
  props: ['cardValue'],
  methods: {
    reveal() {
      const paramVal = this.cardValue === '?' ? 'question' : this.cardValue;
      this.$router.push({ path: `reveal/${paramVal}` });
    },
  },

};
</script>

